* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.register-page, .login-page {
  display: flex;
  align-items: center;
  justify-content: center;

}

.register-form, .login-form {
  width: 500px;
  margin-top: 200px;
}

.register-form h1, .login-form h1 {
  padding-bottom: 30px;
}

.btn {
  margin-top: 20px;
}

.username {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 30px;
}

.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 px gray;
}

.ant-select-selector {
  min-width: 150px;
}

.anticon svg {
  font-size: 20px;
  cursor: pointer;
}

.active-icon {
  color: black;
}

.inactive-icon {
  color: grey;
}

.switch-icons {
  border: 1px solid black;
  border-radius: 5px;
  height: 35px;
}

.switch-icons svg {
  width: 1.4rem;
  height: 1.4rem;
  margin-top: 4px;
}

.row {
  display: flex;
  justify-content: center;
}

.footer {
  margin-top: 80px;
}